/*!重置样式*/

div,
p,
ul,
li,
a,
span,
b,
header,
main,
footer,
em,
img,
input,
textarea,
select,
h1,
h2,
h3,
h4,
h5,
nav,
body {
    margin: 0;
    padding: 0;
}

a {
    color: black;
    text-decoration: none;
}

body {
    overflow-x: hidden;
}

html {
    font-size: 14px;
    font-family: "微软雅黑";
}


/*设置样式变量*/

$bgcolor:#999;

/*! 设置头部样式 */

header {
    width: 100%;
    nav {
        width: 1200px;
        height: 60px;
        margin: auto;
        background-color: white;
        li {
            float: left;
            display: block;
            line-height: 60px;
            list-style: none;
        }
        .logo {
            width: 20%;
            height: 60px;
            a {
                display: inline-block;
                padding: 10px 0;
                img {
                    display: inline-block;
                    width: 100%;
                    height: 100%;
                }
            }
        }
    }
    .nav-box {
        width: 60%;
        line-height: 60px;
        display: flex;
        justify-content: space-around;
        span {
            font-size: 16px;
            display: block;
            text-align: center;
            padding: 0 22px;
             :hover {
                color: $bgcolor;
            }
        }
    }
    .icon {
        width: 19%;
        line-height: 60px;
        display: flex;
        justify-content: space-around;
        span {
            height: 60px;
            display: block;
            text-align: center;
            width: 33%;
            a {
                font-size: 20px;
            }
        }
    }
    .shopcar a:hover {
        color: $bgcolor;
        cursor: pointer;
    }
    .search a:hover {
        color: $bgcolor;
        cursor: pointer;
    }
}

//banner图
main {
    width: 100%;
    .banner {
        width: 100%;
    }
    //设置轮播图两个点击按钮
    #point {
        width: fit-content;
        position: absolute;
        z-index: 50;
        left: 50%;
        transform: translateX(-50%);
        bottom: 5%;
        span {
            display: inline-block;
            width: 50px;
            height: 3px;
            background-color: black;
            margin-left: 5px;
        }
        .active {
            background-color: coral;
        }
    }
    //设置第二张banner图片样式
    .img2 {
        .banner-content {
            position: absolute;
            // z-index: 10;
            top: 30%;
            right: 17%;
            color: #555;
            width: max-content;
             :nth-child(1) {
                font-size: 30px;
                height: 100%;
                margin-left: 10px;
                font-weight: 900;
                margin-bottom: 10px;
            }
            p {
                width: 100%;
                line-height: 30px;
                text-align: center;
            }
             :nth-child(2) {
                font-size: 20px;
                font-weight: 900;
            }
             :nth-child(3) {
                font-size: 16px;
                font-weight: 600;
                line-height: 40px;
            }
             :nth-child(5) {
                margin-top: 20px;
                .by:hover {
                    background-color: #555;
                    color: rgb(248, 248, 248);
                    font-weight: normal;
                }
                a {
                    display: inline-block;
                    font-size: 16px;
                    line-height: 40px;
                    color: #555;
                    border: 1px solid #555;
                    border-radius: 20px;
                    padding: 0 10px;
                    width: 80px;
                }
            }
             :nth-child(6) a {
                display: inline-block;
                color: blue;
                line-height: 40px;
                font-size: 16px;
            }
        }
    }
    //设置第一张banner图样式
    .img1 {
        .banner-content {
            position: absolute;
            // z-index: 10;
            top: 30%;
            left: 17%;
            color: white;
            width: 280px;
            img {
                display: block;
                width: 250px;
                height: 100%;
                margin-left: 10px;
            }
            p {
                width: 100%;
                line-height: 30px;
                text-align: center;
            }
             :nth-child(2) {
                font-size: 20px;
                font-weight: 900;
            }
             :nth-child(3) {
                font-size: 16px;
                font-weight: 600;
                line-height: 40px;
            }
             :nth-child(4) {
                margin-top: 20px;
                a:hover {
                    background-color: white;
                    color: black;
                }
                a {
                    display: inline-block;
                    font-size: 16px;
                    line-height: 40px;
                    color: white;
                    border: 1px solid white;
                    border-radius: 20px;
                    padding: 0 10px;
                    width: 80px;
                }
            }
             :nth-child(5) a {
                border: 0;
                width: fit-content;
                display: inline-block;
                color: blue;
                line-height: 40px;
            }
        }
    }
    .banner-box {
        width: 100%;
        position: relative;
        height: 500px;
        // 设置两个小箭头的样式
        .iconfont {
            display: block;
            position: absolute;
            color: white;
            width: 60px;
            height: 60px;
            border-radius: 50%;
            border: 1px solid white;
            top: 50%;
            transform: translateY(-50%);
            line-height: 60px;
            text-align: center;
            font-size: 30px;
        }
        .right {
            right: 2%;
        }
        .left {
            left: 2%;
        }
        div {
            position: absolute;
        }
    }
    .banner-box a {
        display: block;
        width: 100%;
        img {
            display: block;
            max-width: 100%;
            height: auto;
            line-height: 1px;
        }
    }
    //设置内容区样式
    .main1 {
        width: 100%;
        position: relative;
        img {
            width: 100%;
            height: auto;
        }
        .banner-content {
            position: absolute;
            // z-index: 10;
            top: 30%;
            left: 17%;
            color: white;
            width: max-content;
             :nth-child(1) {
                font-size: 30px;
                height: 100%;
                margin-left: 10px;
                font-weight: 900;
                margin-bottom: 10px;
            }
            p {
                width: 100%;
                line-height: 30px;
                text-align: center;
            }
             :nth-child(2) {
                font-size: 20px;
                font-weight: 900;
            }
             :nth-child(3) {
                font-size: 16px;
                font-weight: 600;
                line-height: 40px;
            }
             :nth-child(5) {
                margin-top: 20px;
                a:hover {
                    background-color: white;
                    color: #555;
                    font-weight: normal;
                }
                a {
                    display: inline-block;
                    font-size: 16px;
                    line-height: 40px;
                    color: white;
                    border: 1px solid white;
                    border-radius: 20px;
                    padding: 0 10px;
                    width: 80px;
                }
            }
             :nth-child(6) a {
                display: inline-block;
                color: blue;
                line-height: 40px;
                font-size: 16px;
            }
        }
    }
}

//内容区2的样式
.main2 {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    div {
        width: 49%;
        position: relative;
        img {
            width: 100%;
            height: auto;
            margin-top: 8px;
        }
    }
    .mainc {
        text-align: center;
        position: absolute;
        top: 10%;
        left: 50%;
        transform: translateX(-50%);
        p {
            color: #fff;
            font-size: 16px;
            line-height: 20px;
        }
    }
}

.banner2 {
    width: 100%;
    margin-top: 8px;
    img {
        display: block;
        width: 100%;
        height: auto;
    }
}

.news {
    background-color: rgba(216, 212, 212, 0.863);
    padding-top: 25px;
    padding-bottom: 50px;
    margin-top: 10px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-around;
    div p:nth-child(2) {
        padding-left: 5px;
        color: #444;
        line-height: 40px;
    }
    div p:nth-child(3) {
        padding-left: 5px;
        color: #222;
        font-size: 20px;
        height: 60px;
    }
    div p:nth-child(4) {
        margin-top: 30px;
        a {
            padding-left: 5px;
            color: rgb(60, 60, 241);
            font-size: 16px;
        }
    }
    div {
        width: 30%;
        img {
            display: block;
            width: 100%;
            height: auto;
            margin-bottom: 30px;
        }
    }
}

//设置footer样式
.after-sale {
    position: relative;
    width: 1200px;
    margin: 50px auto;
    display: flex;
    justify-content: space-around;
    padding-bottom: 40px;
    border-bottom: 1px solid #ccc;
    a:hover {
        color: $bgcolor;
    }
    a {
        display: block;
        text-align: center;
        div {
            line-height: 40px;
            text-align: center;
        }
    }
    .iconfont {
        color: #333;
        line-height: 80px;
        text-align: center;
        font-size: 40px;
        display: inline-block;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        border: 2px solid #333;
    }
    a:hover .iconfont {
        color: $bgcolor;
        border: $bgcolor 2px solid;
    }
}

.foot-nav {
    position: relative;
    width: 1200px;
    margin: 0 auto;
    border-bottom: 1px solid $bgcolor;
    padding-bottom: 60px;
    margin-bottom: 50px;
    ul {
        a {
            color: $bgcolor;
        }
        float: left;
        li {
            list-style: none;
            margin-right: 110px;
            line-height: 30px;
        }
    }
    ul li:nth-child(1) a {
        line-height: 60px;
        font-size: 16px;
        color: #333;
    }
    li:hover a {
        color: #333;
    }
}

.service {
    position: absolute;
    right: 0;
    top: 20px;
    text-align: right;
    color: $bgcolor;
    line-height: 25px;
    .iconfont {
        display: inline-block;
        width: 30px;
        height: 30px;
        border-radius: 50%;
        text-align: center;
        line-height: 30px;
        font-size: 20px;
        margin-right: 10px;
        border: 1px solid $bgcolor;
    }
}

.service p:nth-child(1) span {
    width: 150px;
    height: 50px;
    line-height: 50px;
    background-color: #666;
    border-radius: 25px;
    color: white;
    display: inline-block;
    text-align: center;
}

.service p:nth-child(1):hover span {
    background-color: #333;
}

.service p:nth-child(2) {
    margin-top: 20px;
}

.service p:nth-child(3) {
    color: #333;
    font-size: 25px;
    font-weight: 900;
    line-height: 40px;
}

.service p:nth-child(5) {
    margin-top: 20px;
}

.bottom {
    width: 1200px;
    margin: 0 auto;
    padding-bottom: 50px;
    line-height: 30px;
    color: $bgcolor;
    p {
        text-align: center;
    }
    a {
        color: $bgcolor;
        font-weight: 900;
    }
    a:hover {
        color: #333;
    }
}